/* Import Poppins font for a modern, stylish look */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
    background-color: #121212;
    color: #ffffff;
}

.dashboard {
    padding: 20px;
}

h1, h2 {
    color: #ffffff;
}

.main-content {
    display: flex;
    gap: 20px;
}

/* Grids section styling */
.grids-section {
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex: 2;
}

.grid-wrapper {
    background-color: #1e1e1e;
    padding: 10px;
    border-radius: 8px;
}

/* Use flex for columns, but enforce fixed sizes */
.grid {
    display: flex;
    gap: 5px;
}

.grid-column {
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 50%;
}

/* Each grid cell has fixed dimensions */
.grid-cell {
    background-color: #2c2c2c;
    border: 1px solid #444;
    display: flex;
    align-items: center;
    justify-content: left;
    border-radius: 4px;
    font-size: 1.2rem;
    width: 100%;
    height: 75px;
    padding: 30px;
    box-sizing: border-box;
    overflow: hidden;
}

.grid-input {
    background-color: inherit;
    color: #ffffff;
    font-weight: bold;
    border: none;
    width: 25%;
    height: 400%;
    text-align: left;
    font-size: inherit;
    box-sizing: border-box;
}

/* Sidebar styling */
.sidebar {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.progress-container,
.messages-container,
.export-container {
    background-color: #1e1e1e;
    padding: 10px;
    border-radius: 8px;
}

/* Horizontal progress bar styling */
.progress-bar-container {
    margin-bottom: 10px;
}

.progress-bar-label {
    font-size: 0.9rem;
    margin-bottom: 5px;
}

.progress-bar {
    background-color: #2c2c2c;
    border-radius: 4px;
    height: 12px;
    width: 100%;
}

.progress-bar-fill {
    background-image: linear-gradient(to right, #4169E1, #87CEFA);
    height: 100%;
    border-radius: 4px;
}

/* Button styling */
button {
    background-color: #1E90FF;
    color: #fff;
    border: none;
    padding: 10px;
    border-radius: 4px;
    cursor: pointer;
}

button:hover {
    background-color: #4169E1;
}

.autocomplete-wrapper {
    position: absolute;
    width: 100%;
}

.suggestions-list {
    list-style: none;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: #2c2c2c;
    border: 1px solid #444;
    max-height: 150px;
    width: 25%;
    overflow-y: auto;
    z-index: 100;  /* Increase z-index so it appears above other elements */
}

.suggestions-list li {
    padding: 5px 10px;
    cursor: pointer;
}

.suggestions-list li:hover {
    background-color: #444;
}

